.Tile{
  width: 27vw;
  height: 18vw;
  font-size: 1.7vw; 
  display: inline-block;
  position: relative;
  overflow: hidden;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: 20px;
  -webkit-transition: ease-in-out 0.2s;
  -o-transition: ease-in-out 0.2s;
  transition: ease-in-out 0.2s;
  margin: 5px;
  cursor: pointer;
  -webkit-animation: tile-move .5s ease-in-out;
          animation: tile-move .5s ease-in-out;
}

.imgNews{  
  display: block;
  position: absolute;
  background-color: black;
  top: 50%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1;
  opacity: .7;
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.titleNews{
  position: absolute;
  font-size: 30px;
  padding: 10px;
  text-align: left;
  opacity: 0;
  z-index: 2;
  color: #9eb3ff;
  border-bottom: 1px solid #9eb3ff;
  text-shadow:  1px 1px black;
}

.descriptionNews{
  position: absolute;
  bottom: 10px;
  opacity: 0;
  font-size: 20px;
  padding: 10px;
  z-index: 2;
  border-radius: 10px;
  color: white;
  background-color: rgba(0, 0, 0, 0.253);
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.Tile:hover .imgNews{
  cursor: pointer;
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
  -webkit-animation: rotate .3s ease-in-out forwards;
          animation: rotate .3s ease-in-out forwards;
}

@-webkit-keyframes rotate{
  to{
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0;
  }
}

@keyframes rotate{
  to{
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0;
  }
}

.Tile:hover > .titleNews{
  opacity: 1;
}

.Tile:hover > .descriptionNews{
  opacity: 1;
}

@media screen and (max-width: 1700px) {
  .titleNews{
    font-size: 22px;
  }
}

@media screen and (max-width: 1200px) {
   .Tile {
     width: 40vw;
     height: 25vw;
   }

   .titleNews{
     font-size: 18px;
   }
}

@media screen and (max-width: 1000px) {
     .Tile {
     width: 70vw;
     height: 40vw;
     margin-bottom: 10px;
   }

   .titleNews{
     font-size: 20px;
   }
}

.loading {
  height: 100vh;
  width: 100vw;
  color: #9eb3ff;
  text-align: center;
  font-size: 80px;
  margin-top: 200px;
}

@-webkit-keyframes tile-move{
  from{
    opacity: 0;
  }
}

@keyframes tile-move{
  from{
    opacity: 0;
  }
}

@-webkit-keyframes dot-move {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  25%{
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  50% {  
    -webkit-transform: translateY(0);  
            transform: translateY(0);
  }

  75%{
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px)
  }

  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes dot-move {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  25%{
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  50% {  
    -webkit-transform: translateY(0);  
            transform: translateY(0);
  }

  75%{
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px)
  }

  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.dot{
  width: 25px;
  height: 25px;
  margin: 10px;
  display: inline-block;
  background-color: #9eb3ff;
  border-radius: 50%;
  -webkit-animation: dot-move 1s infinite linear
  ;
          animation: dot-move 1s infinite linear
  ;
}

.dot1{
  -webkit-animation-delay: 0;
          animation-delay: 0;
}

.dot2{
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
}

.dot3{
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}


